/*** Statr Bootstrap Carousle***/

/* Overriding Controls */
.mainn-carusel-costume {
  &:hover .carousel-control-next,
  &:hover .carousel-control-prev {
    opacity: 1;
  }
}

.carousel-item {
  max-height: 700px;
  .view {
    line-height: 0;
  }
  .view__img {
    width: 100%;
    height: 100%;
  }
  .video-fluid {
    width: 100%;
  }
}

.carousel-control-next,
.carousel-control-prev {
  position: absolute;
  top: 50%;
  bottom: 50%;
  z-index: 9999;
  width: auto;
  color: #fff;
  text-align: center;
  opacity: 0;
  transition: opacity 0.7s ease;
  &:hover {
    transition: opacity 0.3s ease;
    opacity: 0.5 !important;
  }
}
.carousel-control-next {
  right: 40px;
}
.carousel-control-prev {
  left: 40px;
}
.carousel-control-next-icon,
.carousel-control-prev-icon {
  background-image: none;
  width: 25px;
  height: 25px;
  box-shadow: -3px 3px 0 #fff;
}
.carousel-control-next-icon {
  transform: rotate(225deg);
}
.carousel-control-prev-icon {
  transform: rotate(45deg);
}
/* Overriding Controls */
/* Start Caption */
.carousel-caption__frist-caption {
  background-color: rgba(41, 41, 41, 0.555);
  width: 25%;
  top: 15%;
  bottom: auto;
  left: 5%;
  padding: 0;
  margin: 0;
  opacity: 0;
  animation-duration: 6s;
  animation-name: frist-caption-animate;
}
@keyframes frist-caption-animate {
  0% {
  }
  20% {
    opacity: 0;
    left: 5%;
  }
  35% {
    opacity: 1;
    left: 10%;
  }
  55% {
    opacity: 1;
    left: 10%;
  }
  90% {
    opacity: 1;
    left: 10%;
  }

  100% {
    left: 13%;
    opacity: 0;
  }
}
.first-animate__img {
  width: 100%;
}

.carousel-second-caption--shopnow,
.carousel-third-caption--shopnow {
  left: 35%;
  right: auto;
  width: 12%;
  min-width: 50px;
  bottom: auto;
  background-color: rgba(255, 255, 255, 0.8);
  border: 1px solid rgb(109, 109, 109);
  padding: 0;
  opacity: 0;
  animation-duration: 7s;
}
.carousel-second-caption--shopnow {
  animation-name: second-caption-animate;
}

@keyframes second-caption-animate {
  0% {
  }
  20% {
    opacity: 0;
    top: 40%;
  }
  35% {
    opacity: 1;
    top: 25%;
  }
  80% {
    opacity: 1;
    top: 25%;
  }

  100% {
    top: 40%;
    opacity: 0;
  }
}
.second-animate__img {
  width: 100%;
  height: 100%;
}
.carousel-third-caption--shopnow {
  right: 10%;
  left: auto;
  animation-name: third-caption-animate;
}
@keyframes third-caption-animate {
  0% {
  }
  20% {
    opacity: 0;
    top: 60%;
  }
  35% {
    opacity: 1;
    top: 50%;
  }
  80% {
    opacity: 1;
    top: 50%;
  }

  100% {
    top: 60%;
    opacity: 0;
  }
}

.third-animate__img {
  width: 100%;
  height: 100%;
}

/* End Caption */
/* Overriding Indicators */
.carousel-indicators {
  bottom: 10px;
  z-index: 9999;
}
.carousel-indicators li {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: 2px solid #fff;
  background-color: transparent;
  opacity: 1;
  position: relative;
  z-index: 9998;
  overflow: hidden;
  span {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 12px;
    height: 0px;

    background-color: #fff;
    z-index: 9999;
    transition: height 0.2s;
  }
}
.carousel-indicators .active {
  span {
    height: 14px;
  }
}

/* Overriding Indicators */

/*** End Bootstrap Caroulse ***/

/* Start top feature with image hover */
.top-feature {
  margin-top: 20px;
}
.top-feature__feat {
  @media #{$maxSmall} {
    margin-top: 20px;
  }
  cursor: pointer;
  overflow: hidden;
  position: relative;
  text-align: center;
  z-index: 3;
}
.top-feature__feat--hover {
  &:hover .top-feature__img {
    transform: scale(1.2);
  }
}
.top-feature__feat--hover:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
}
.feat__text-overlay--up {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(238, 238, 238, 0.589);
  padding: 20px;
  text-transform: uppercase;
  z-index: 2;
  visibility: hidden;
}
.feat__text-overlay--animateup {
  visibility: visible;
  animation: text-overlay-animate 1.5s forwards;
}
@keyframes text-overlay-animate {
  from {
    opacity: 0.2;
    top: 25%;
  }
  to {
    opacity: 1;
    top: 50%;
  }
}

.feat__text-overlay--top {
  color: #fff;
  font-weight: bold;
  font-size: 20px;
  line-height: 1;
}
.text-overlay__divider {
  color: #eee;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #eee;
}
.feat__text-overlay--bottom {
  color: #eeeeeec9;
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
.top-feature__img {
  position: relative;
  width: 100%;
  transition: transform 0.3s ease-in-out;
}
/* End top feature with image hover */

/*** Start Feature Item  ***/

/* Start Override glide */
.featured-items__featured-container{
  transition: box-shadow 0.3s ease;
  &:hover {
    box-shadow: 0px 0px 1px 0px rgba(153,145,153,1);
   
  }
}
.glide__slide--costume {

  img {
    width: 100%;
  }
}
.glide__arrow-costume--left,
.glide__arrow-costume--right {
  background-color: rgba(255, 255, 255, 0.582);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  color: #4e4b4b;
}
.glide__arrow-costume--left {
  left: -10px;
}
.glide__arrow-costume--right {
  right: -10px;
}

.glide__arrow-icon--left,
.glide__arrow-icon--right {
  width: 12px;
  height: 12px;
}

/* End Override glide */

.featured-container__featured-details {
  padding: 20px 20px;
  background-color: #fff;
  position: relative;
}
.featured-details__product-name {
  display: block;
  color: #3b3b3b;
  font-size: 17px;
  text-transform: uppercase;
  transition: color 0.4s ease;
  &:hover {
    color: $redColor;
    text-decoration-line: none;
  }
}
.featured-details__product-category {
  color: #bbb;
  font-size: 12px;
  text-transform: uppercase;
  transition: color 0.4s ease;
  &:hover {
    color: $redColor;
    text-decoration-line: none;
  }
}
.featured-details__product-divider {
  border-top: 2px solid #cccccc66;
}
.featured-details__product-price {
  color: $redColor;
  font-size: 16px;
}
.featured-details__product-add {
  border-radius: 50%;
  background-color: #ccc;
  font-size: 18px;
  padding: 2px;
  text-align: center;
  color: #fff;
  border: none;
  outline: none;
}
.featured-details__product-btn--add {
  width: 20px;
  height: 20px;
  fill: #d2d2d2;
  padding: 1px;
  position: absolute;
  right: 20px;
}
/* Start Inner Slider */
.swiper-container {
  width: 100%;
  height: 100%;

  &:hover .swiper-button-next--costume {
    right: 0;
  }
  &:hover .swiper-button-prev--costome {
    left: 0;
  }
}

.swiper-button-next--costume {
  right: 0;
}
.swiper-button-prev--costome {
  left: 0;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button-next--costume,
.swiper-button-prev--costome {
  background-image: none;
  background-color: #eeeeeeb3;
  padding-top: 13px;
  text-align: center;
  height: 53px;

  &:hover {
    background-color: #e9e9e9;
  }
}
@media #{$minSmall} {
  .swiper-button-next--costume {
    transition: all 0.3s ease;
    right: -27px;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
  }
  .swiper-button-prev--costome {
    transition: all 0.3s ease;
    left: -27px;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
  }
}

.swiper__arrow-icon--right,
.swiper__arrow-icon--left {
  width: 12px;
  height: 12px;
}
/* End Inner Slider */

/*** End Feature Item  ***/
