/***Start Latest Item ***/

/*** Start ecommerce ***/
.ecommerce {
  background-color: #fff;
  padding: 15px;
  margin-bottom: 20px;
}
.ecommerce-containt {
  border: 1.4px solid #ccc;
  padding: 35px;
}

.ecommerce__heading {
  color: #3b3b3b;
  text-transform: uppercase;
  margin-bottom: 0;
}
.ecommerce__text {
  color: #bbb;
  font-size: 1rem;
  margin-bottom: 0;
}
.ecommerce__btn {
  margin-left: auto;
  margin-top: 10px;
}
@media #{$maxMobile} {
  .ecommerce {
    text-align: center;
  }
  .ecommerce__heading {
    font-size: 18px;
  }

  .ecommerce__btn {
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
  }
}

/*** End ecommerce ***/
/* Start bottom feature with image hover*/

.feat__text-overlay--down {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(238, 238, 238, 0.589);
  padding: 20px;
  text-transform: uppercase;
  z-index: 2;
  visibility: hidden;
}
.feat__text-overlay--animatedown {
  visibility: visible;
  animation: text-overlay-animate-down 1.5s forwards;
}
@keyframes text-overlay-animate-down {
  from {
    opacity: 0.2;
    top: 75%;
  }
  to {
    opacity: 1;
    top: 50%;
  }
}

/* End bottom feature with image hover*/
/* Start form the blog */
.blog {
  margin-top: 20px;
}
.blog__main-box {
  background-color: #fff;
  padding: 20px;
  margin-bottom: 25px;
}
.main-box__link {
  display: block;
  position: relative;
  z-index: 998;

  .link__image {
    width: 100%;
    height: 100%;
  }

  .link__hover {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 50%;
    bottom: 50%;
    background-color: #0e070296;
    z-index: 999;
    transition: all 0.2s ease;
    overflow: hidden;
  }

  .link__text {
    position: absolute;
    width: 100px;
    top: calc(50% - 7px);
    left: calc(50% - 50px);
    z-index: 1000;
    color: #fff;
    font-size: 11px;
    text-align: center;
    text-transform: uppercase;
    opacity: 0;
  }

  &:hover .link__hover {
    top: 34%;
    bottom: 34%;
    z-index: 999;
  }
  &:hover .link__text {
    opacity: 1;
    transition-property: opacity;
    transition-delay: 0.2s;
    transition-duration: 0.2s;
  }
}

.blog__link {
  display: block;
  color: $black;
  margin-top: 15px;
  transition: color 0.3s ease;

  &:hover {
    text-decoration: none;
    color: $redColor;
  }
}

.blog__date {
  font-size: 12px;
  color: #999999;
}
.blog__subject {
  font-size: 14px;
  color: #888;
  margin-bottom: 0;
}
/* End form the blog */
/* Start Say Us */
.say-us {
  background-color: #fff;
  padding: 15px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.say-us__box {
  border: 1.4px solid #ccc;
  padding: 15px;

  .carousel-costume {
    position: static;
  }
  .carousel-inner {
    position: static;
  }
  .carousel-item {
    visibility: hidden;
    opacity: 0;
  }
  .active {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.2s ease !important;
    color: $redColor;
  }
}

.say-us__box-slide {
  color: #000;
  position: static;
  padding: 5px;
}

.box-slide__container {
  display: flex;
}
.box-slide__img {
  img {
    width: 100%;
    max-width: 100px;
    min-width: 50px;
    border-radius: 50%;
  }
}

.box-slide__content {
  text-align: left;
  padding-left: 15px;
  padding-right: 15px;
}
.content__person-say {
  font-style: italic;
  color: #888;
  margin-bottom: 5px;
}
.content__person-name {
  &:hover {
    color: $redColor;
  }
}

.carousel-indicators--costume {
  bottom: 0px;
  margin-bottom: 0;

  .active {
    background-color: #888;
  }
  li {
    border: none;
    background-color: #d6d2d2;
    width: 9px;
    height: 9px;
  }
}
/* End Say Us */
/* Start Offer Table */
.offer-table__offer-box {
  background-color: #fff;
  padding: 25px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.offer-box__heading {
  text-transform: uppercase;
  font-weight: 600;

  color: #4e4b4b;
  margin-bottom: 20px;
}

.offer-box__item-box {
  border-top: 1px solid #eee;
  display: flex;
  padding-top: 15px;
  padding-bottom: 15px;

  &:first-of-type {
    border-top: 1.5px solid #eee;
  }
  &:last-of-type {
    padding-bottom: 0px;
  }
}

.item-box__img-box {
  padding-right: 6px;
  img {
    width: 60px;
  }
}

.details-box__product-name {
  font-size: 13px;
  color: #4e4b4b;
  transition: color 0.5s ease;
  margin-bottom: 7px;

  &:hover {
    color: $redColor;
    cursor: pointer;
  }
}

.details-box__product-price {
  font-size: 13px;
  color: $redColor;
  margin: 0;
}

/* End Offer Table */
