/*** Start Footer ***/
/* Start Top Footer */
.footer {
  margin-top: 60px;
}
.top-footer__footer-box {
  margin-bottom: 30px;
}

.footer-box__heading {
  color: #444343;
  margin-bottom: 15px;
  font-size: 19px;
  text-transform: uppercase;
}
.footer-box__text {
  font-size: 14px;
  color: #757575;
}

.footer-box__social-list {
  display: flex;
  margin-top: 10px;
}
.social-list__li-icon {
  margin-right: 10px;
  background-color: #ccc;
  border-radius: 50%;
  transition: all 0.8s ease;

  .social-list__svg-icon {
    border: 1px solid #ccc;
    border-radius: 50%;
    fill: #eee;
    width: 30px;
    height: 30px;
    transition: all 0.8s ease;
  }

  &:hover {
    width: 30px;
    height: 30px;
    background-color: #fff;
    cursor: pointer;

    .social-list__svg-icon {
      border: none;
    }
    .social-list__svg-icon--facebook {
      fill: #3b5998;
    }
    .social-list__svg-icon--instagram {
      fill: #3f729b;
    }
    .social-list__svg-icon--youtube {
      fill: #c4302b;
    }
    .social-list__svg-icon--twitter {
      fill: #00acee;
    }
    .social-list__svg-icon--plus {
      fill: #db4437;
    }
  }
}
.footer-box__tags {
  text-decoration: none;
  border: 1px solid #ccc;
  padding: 3px 6px;
  color: #4c4c4c;
  display: inline-block;
  text-transform: uppercase;
  margin-bottom: 7px;
  margin-right: 3px;
  font-size: 11px;
  transition: all 0.6s ease;

  &:hover {
    text-decoration: none;
    background-color: #fff;
    color: $redColor;
  }
}

.footer-box__subscribe-form {
  margin-top: 30px;
  display: flex;
}
.footer-box__ssubscribe-input {
  padding: 6.5px 10px;
  border: none;
  font-size: 17px;
  color: #757575;
  outline: none;
  width: 65%;
}
.footer-box__ssubscribe-btn {
  @extend.ecommerce__btn;
  margin-top: 0px;
  height: 38px;
  min-width: 100px;
  width: 35%;
  text-transform: capitalize;
}

/* End Top Footer */
/* Footer Divider */
.footer__divider {
  border-left: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding-top: 1px;
}
/* Footer Divider */
.footer__bottom-footer {
  margin-top: 10px;
}

.bottom-footer__links-list {
  display: flex;
  margin-bottom: 0;
  @media #{$maxMobile} {
    display: block;
  }
}

.footer-links__list-item {
  text-transform: uppercase;
  color: #757575;
  font-size: 12px;
  margin-bottom: 0px;
  transition: color 0.7s ease;


  &:hover {
    color: $redColor;
    cursor: pointer;
  }
  @media #{$maxMobile} {
    font-size: 15px;
    padding-top: 7px;
    padding-bottom: 7px;
    &:not(:last-of-type) {
      border-bottom: 1px solid #ddd;
    }
  }
}

.footer-links__list-item--active {
  color: $redColor;
}

@media #{$minSmall} {
  .footer-links__list-item:not(:last-of-type)::after {
    content: "|";
    color: #ccc;
    margin-left: 10px;
    margin-right: 10px;
  }
}

.bottom-footer__copyright {

  p {
    text-transform: uppercase;
    color: #2b2b2b;
    font-size: 12px;
    padding-top: 10px;
  }
  span {
    transition: color 0.7s ease;
    &:hover {
      color: $redColor;
      cursor: pointer;
    }
  }
  @media #{$maxMobile} {
    text-align: center;
  }
}
.bottom-footer__payment-icon {
  text-align: right;
  margin-bottom: 10px;
  @media #{$maxMobile} {
    margin-bottom: 0;
    text-align: center;
  }
}
.payment-icon__img {
  width: 50px;
  height: 40px;
  margin-left:15px; 
  filter: grayscale(100%);
  transition: .3s ease-in-out;
  &:hover{
    filter: grayscale(0);
    cursor: pointer;

  }

}

/*** End Footer ***/
