.toggle-header__btn{
    height: 35px;
    padding-left: 17px;
    text-transform: uppercase;
    margin-bottom: 10px;
    font-size: 11px;
    font-weight: bold;
    line-height: 35px;
    border: none;
    color: #FFF;
    position: relative;
    @include prefixer(transition,.4s,webkit moz o ms);

    &:hover{
        cursor: pointer;
    }


}

.toggle-header__btn--gray{
    background-color: $greyColor;

    &:hover{
        background-color:$hovergreycolor;

    }
}

.toggle-header__btn--red{
   background-color: $redColor; 

   &:hover{
    background-color: $hoverRedColor;
   }
}

.ecommerce__btn{
    @extend.toggle-header__btn;
    background-color: $black; 
    text-align: center;
    padding: 0;
    margin-bottom: 0;
    width: 130px;

    
       &:hover{
       opacity: .8;
       }

}

   

