/***** start toggle header****/
.toggle-header {
  background-color: $black;
}
#toggle-header {
  transition: height 0.6s;
  overflow: hidden;
  @media #{$maxSmall} {
    display: none;
  }
}
.toggle-header__row {
  padding: 30px;
}
.toggle-header__product {
  margin-right: 60px;
}
.toggle-header__pragraph {
  color: #4e4e4e;
  font-size: 28px;

  margin-top: 25px;
  margin-bottom: 30px;
  text-align: center;
}
#header-btn__basket-icon {
  fill: #fff;
}
.toggle-header__icon {
  width: 17px;
  height: 15px;
  position: absolute;
  top: 26%;
  right: 20px;
  transform: rotateY(180deg);
}
.controls-container__subtotal {
  text-align: right;
}
.controls__subtotal {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 300;
}
.controls__subtotal--text {
  color: #fff;
  font-size: 12px;
}
.controls__subtotal--number {
  color: $redColor;
  padding-left: 5px;
  font-size: 14px;
}
/**** End toggle header****/

/**** Start Header****/
.header {
  background-color: #fff;
  @media #{$maxSmall} {
    [class*="col-"] {
      padding-left: 0;
      padding-right: 0;
    }
    .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
.header .container {
  position: relative;
  @media #{$maxSmall} {
    width: 100%;
    max-width: none;
    padding: 0;
  }
}
.header__logo {
  text-align: center;
  @media #{$maxSmall} {
    text-align: left;
  }
}
.logo__heading {
  padding-top: 40px;
  padding-bottom: 40px;
  margin-bottom: 0px;
  text-transform: uppercase;
  font-size: 32px;
  @media #{$maxSmall} {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 15px;
    margin-bottom: 0px;
    text-transform: uppercase;
  }
}
.header__btn-toggle {
  @media #{$maxSmall} {
    position: absolute;
    top: 20px;
    right: 20px;
    display: inline-block;
    width: 42px;
    height: 43px;
    padding-top: 14px;
    transition: background-color 0.3s ease-in-out;
    &:hover {
      background-color: $black;
    }
    &:hover span {
      background-color: #fff;
    }
    span {
      display: block;
      width: 15px;
      height: 2px;
      background-color: $black;
      margin-top: 2px;
      margin-left: auto;
      margin-right: auto;
      transition: background-color 0.3s ease-in-out;
    }
  }
}
.navbar {
  display: flex;
  justify-content: center;
}
.navigation {
  @media #{$maxSmall} {
    background-color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10000;
    transition: max-height 0.07s ;
    transition-delay: .25s;
    max-height: 0;
    overflow: hidden;
  }
}
.list-btn__icon{
  transition:transform .3s;
  transform: rotate(0deg);
}
.list-btn__icon--active{

  transform: rotate(45deg);
}




.sticky {
  @media #{$minMedium} {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99999;
    background-color: #fff;
  }
}
.header__navbar {
  display: flex;
  justify-content: center;
  @media #{$maxSmall} {
    display: block;
  }
}
.navbar__list {
  text-align: left;
  border-bottom: 1px solid#e0e0e0;
  position: relative;
  z-index: 9998;
}
.navbar__list-item {
  display: inline-block;
  position: relative;
  font-size: 16px;
  text-transform: uppercase;
  padding: 13px 10px;
  
  cursor: pointer;
  .navbar__anchor {
    border-bottom: 1px solid transparent;
    padding-bottom:7px;
   
  }
  @media #{$maxSmall} {
    padding: 0;
    display: block;
    .navbar__anchor {
      background-color: #fff;
      border-bottom: 1px solid #e0e0e0;
      padding: 10px 0 10px 15px;
      display: block;
      &:hover {
        color: $hoverRedColor !important;
      }
    }
  }
  @media #{$minMedium} {
    &:hover .navbar__anchor {
      transition: padding-bottom 0.15s ;
      border-bottom: 1px solid #979595;
      padding-bottom: 5px;
    }
    &:not(:first-child)::before {
      content: "";
      margin-left: 5px;
    }
    &:not(:last-child)::after {
      content: "";
      border: solid #615f5f;
      border-width: 0 1.4px 1.4px 0;
      display: inline-block;
      padding: 2.5px;
      transform: rotate(45deg);
      position: relative;
      top: -4px;
      margin-left: 8px;
    }
  }
}
.navbar__list-btn {
  display: none;
  transition: background-color 0.5s;
  @media #{$maxSmall} {
    display: block;
    padding: 10px 16px 11px 17px;
    position: absolute;
    border-bottom: 1px solid #e0e0e0;
    border-left: 1px solid #e0e0e0;
    top: -1px;
    right: 0;
    svg {
      width: 12px;
      height: 12px;
    }
  }
  &:hover {
    background-color: #222;
    #list-btn__icon {
      fill: #fff;
    }
  }
}
.list-item__sub-list {
  position: absolute;
  top: 55px;
  padding-top: 10px;
  width: 200px;
  background-color: #fff;
  box-shadow: 0px 0px 1px 1px rgba(250, 250, 250, 0.52);
  visibility: hidden;
  @media #{$maxSmall} {
    padding-top: 0;
    visibility: visible;
    position: static;
    width: 100%;
    max-height: 0px;
    transition: max-height 0.4s;
    overflow: hidden;
  }
}
.sub-list__item {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  font-size: 13px;
  color: #615f5f;
  &:hover {
    color: $redColor;
    transition: color 0.5s;
  }
  @media #{$maxSmall} {
    padding: 13px 0px 12px 35px;
    background-color: #f7f7f7;
    border-bottom: 1px solid #eee;
    color: $black;
  }
}
.navbar__list-item:hover .list-item__sub-list {
  @media #{$minMedium} {
    visibility: visible;
    animation: sub-list-trnaslate .3s forwards;
  }
}
@keyframes sub-list-trnaslate {
  from {
    opacity: 0;
    transform: translateY(0px);
  }
  to {
    opacity: 1;
    transform: translateY(-8px);
  }
}
@media #{$maxSmall} {
  .navbar__search {
    margin-bottom: 0px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: row-reverse;
  }
  .search__list {
    width: 100%;
    form {
      height: 100%;
      padding-left: 5px;
    }
  }
  .search__icon {
    padding: 11px 16px;
    background-color: $black;
  }
  #search__icon--hover {
    fill: #fff;
  }
  .search__input {
    height: 100%;
    width: 100%;
    border: none;
    outline: none;
    background-color: #f7f7f7;
    padding-left: 12px;
    font-size: larger;
  }
}
@media #{$minMedium} {
  .navbar__search {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 25px;

    border-bottom: 1px solid #e0e0e0;
    position: relative;
  }
  .search__icon {
    display: inline;
    padding: 10px 5px;
    position: relative;
    z-index: 10000;
    left: 7px;
    svg {
      margin-top: 5px;
    }
    &:hover {
      cursor: pointer;
    }
    &:hover #search__icon--hover {
      fill: $hoverRedColor;
    }
  }
  .search__list {
    border: 1px solid transparent;
    padding-left: 25px;
    position: absolute;
    top: 4px;
    left: 2px;
    height: 40px;
    opacity: 0.6;
    transition: opacity 0.2s ease-in-out;
  }
  .search__list--active {
    border: 1px solid #eee;
    opacity: 1;
    z-index: 9999;
  }
  .search__input {
    height: 32px;
    width: 180px;
    margin-top: 5px;
    border: none;
    outline: none;
    visibility: hidden;
  }
  .search__input--active {
    visibility: visible;
  }
}
.navbar__anchor{
  &:hover{
    text-decoration: none;
    &:hover{
      color: $black;
    }
  }
}
.navbar__anchor--active{
  color: $redColor ;
  &:hover{
   color:$redColor !important;
  }
}
.navbar__bottom {
  display: flex;
  justify-content: center;
}
.bottom__list-item {
  display: inline-block;
  padding: 0 6px;
  font-weight: 400;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 11px;
  
  &:not(:last-child):after {
    content: "|";
    padding-left: 10px;
    color: #b6b6b6;
  }

  @media #{$maxSmall}{
    &:hover {
      color: $hoverRedColor;
      #bottom-listitem__heart{
        fill: $hoverRedColor;
      }
    }

  }


  @media #{$minMedium}{
    font-size: 12.5px;
    opacity: 0.5;
    transition: opacity 0.5s ease-in-out;
    &:hover {
      opacity: 1;

    }
  }
  
}

.bottom-listitem__heart {
  width: 10px;
  height:auto;
  margin-bottom: 2.3px;
  margin-left: 3px;
}



/* Toggle Button*/
.flag {
  color: white;
}
#flag {
  width: 64px;
  min-height: 80px;
  box-sizing: content-box;
  padding-top: 15px;
  position: relative;
  background: $black;
  color: white;
  font-size: 11px;
  letter-spacing: 0.2em;
  text-align: center;
  text-transform: uppercase;
  transition: padding 0.15s;
  &:hover {
    padding-bottom: 7px;
    cursor: pointer;
  }
  &:hover .toggle-button__icon {
    padding-top: 7px;
    
  }
  @media #{$maxSmall} {
    display: none;
  }
}
#flag:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border-bottom: 9px solid #fff;
  border-left: 31px solid transparent;
  border-right: 35px solid transparent;
}
.header__toggle-button {
  z-index: 9999;
  position: absolute;
  top: 0;
  right: 0;
}
.toggle-button__icon,
.toggle-button__number {
  display: block;
  margin-top: 2px;
  transition: padding-top .15s;
}
.toggle-button__ico {
  width: 23px;
  height: 24px;
}
#toggle-button__icon--svg {
  fill: #fff;
}
.toggle-button__number {
  font-size: 19px;
  font-weight: bold;
  margin-top: 5px;
}
/**** End Header****/
