/*
**Media Query Mixin For Help
*/

@mixin maxSmall {
    @media (max-width:768px) {
        @content;        
    }
}
@mixin minSmall {
    @media (min-width:768px) {
        @content;        
    }
}
@mixin mdiumeSmall {
    @media (min-width:991px) {
        @content;        
    }
}

/*
**Prefix Mixin
**Auto Add Prefixes To propeties
*/ 
@mixin prefixer($property, $value,$prefixes:() ){
    @each $perfix in $prefixes{
        #{'-' + $perfix +'-' + $property}: $value;
    }
    #{$property}: $value;
    

}