/* Start Main Divider */
.main-divider {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  position: relative;
  z-index: 99;
  &::after {
    content: "";
    border-top: 2px solid $black;
    border-bottom: 1px solid $black;
    position: absolute;
    padding-bottom: 2px;
    top: 50%;
    left: 0;
    right: 0;
    z-index: 100;
    color: $black;
  }
}
.main-divider__heading,
.main-divider__text {
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: #eee;
  width: 250px;
  position: relative;
  z-index: 101;
  padding-left: 5px;
  padding-right: 5px;
  text-transform: uppercase;
}
.main-divider__heading {
  color: #3b3b3b;
}
.main-divider__text {
  color: #888;
  font-size: 12px;
}
/* End Main Divider */